@use '../variables/breakpoints.scss';
@use '../mixins/grid.scss';

.kd-grid {
	@include grid.grid;

	&--no-gap {
		gap: 0;
	}

	&--align-left {
		margin: 0;
	}

	&--no-max {
		max-width: none;
	}

	// generate sm (mobile) classes
	@for $i from 1 through 4 {
		&__col--sm-#{$i} {
			@include grid.grid-col($i);
		}
	}

	// generate md (tablet) classes
	@for $i from 1 through 8 {
		&__col--md-#{$i} {
			@media (min-width: breakpoints.$bp-md) {
				@include grid.grid-col($i);
			}
		}
	}

	// use 'auto' when columns are custom-defined at the grid level
	&__col--md-auto {
		@media (min-width: breakpoints.$bp-md) {
			@include grid.grid-col(1);
		}
	}

	// generate lg, xl, max (desktop) classes
	@for $i from 1 through 12 {
		&__col--lg-#{$i} {
			@media (min-width: breakpoints.$bp-lg) {
				@include grid.grid-col($i);
			}
		}

		&__col--xl-#{$i} {
			@media (min-width: breakpoints.$bp-xl) {
				@include grid.grid-col($i);
			}
		}

		&__col--max-#{$i} {
			@media (min-width: breakpoints.$bp-max) {
				@include grid.grid-col($i);
			}
		}
	}

	// use 'auto' when columns are custom-defined at the grid level
	&__col--lg-auto {
		@media (min-width: breakpoints.$bp-lg) {
			@include grid.grid-col(1);
		}
	}

}
