@use '../variables/breakpoints.scss';

@mixin type-hero-01 {
	font-family: var(--kd-font-family-primary);
	font-size: var(--kd-font-size-hero-1-sm);
	line-height: var(--kd-line-height-hero-1-sm);
	font-weight: var(--kd-font-weight-ultralight);
	letter-spacing: var(--kd-letter-spacing-1);

	@media (min-width: breakpoints.$bp-md) {
		font-size: var(--kd-font-size-hero-1-md);
		line-height: var(--kd-line-height-hero-1-md);
	}

	@media (min-width: breakpoints.$bp-lg) {
		font-size: var(--kd-font-size-hero-1-lg);
		line-height: var(--kd-line-height-hero-1-lg);
	}

	@media (min-width: breakpoints.$bp-xl) {
		font-size: var(--kd-font-size-hero-1-xlg);
		line-height: var(--kd-line-height-hero-1-xlg);
	}

	@media (min-width: breakpoints.$bp-max) {
		font-size: var(--kd-font-size-hero-1-max);
		line-height: var(--kd-line-height-hero-1-max);
	}
}

