/* box sizing rules */
*,
*::before,
*::after {
	box-sizing: border-box;
}

html, body {
	overflow-x: clip;
}

/* remove default margin */
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
figure,
blockquote,
dl,
dd {
	margin: 0;
}

/* remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role='list'],
ol[role='list'] {
	list-style: none;
}

/* make images easier to work with */
img,
picture {
	max-width: 100%;
	display: block;
}
